.posts {
    margin-bottom: 8rem;
    @include breakpoint($mobile-bp) {
        margin-bottom: 2rem;
    }
}
.post {
    background-color: $primary;
    box-shadow: 0 15px 25px rgba($black, 0.35);
    padding: 2.5rem;
    margin-bottom: 3rem;
    border-radius: 5px;

    @include breakpoint($mobile-bp) {
        padding: 1.5rem;
    }
    &__header {
        margin: 0 0 2.5rem;

        &--preview {
            margin: -2.5rem -2.5rem 2.5rem;
            @include breakpoint($mobile-bp) {
                margin-left: -1.5rem;
                margin-right: -1.5rem;
            }
        }
    }
    &__preview {
        display: block;
        max-width: 100%;
        height: auto;
    }
    &__content {
        margin-bottom: 2rem;
    }
    &__subtitle {
        font-size: 1.6rem;
        color: $white;
        font-weight: 500;
        margin-bottom: 2rem;
        margin-top: 3rem;
    }
    &__title {
        margin-bottom: 1.5rem;
        font-size: 1.8rem;
        font-weight: 500;
        line-height: 1.4;
        &--open {
            margin-bottom: .75rem;
        }
        a:hover {
            text-decoration: underline;
        }
    }
    &__description {
        font-size: 1.4rem;
        color: #d2d2d2;
    }
    &__text {
        padding-bottom: 1rem;
        color: $white;
        border-bottom: 1px solid #1c1c1c;
        p {
            margin-bottom: 2rem;
        }
        h2,
        h3,
        h4,
        h5,
        h6 {
            font-size: 2em;
            font-weight: 700;
            margin-bottom: 2rem;
        }
        h2 {
            font-size: 1.5rem;
        }
        h3 {
            font-size: 1.25rem;
        }
        h4 {
            font-size: 1.15rem;
        }
        h5,
        h6 {
            font-size: 1rem;
        }
        ul,
        ol {
            margin-bottom: 2rem;
        }
        ol {
            list-style: decimal inside;
        }
        table {
            width: 100%;
        }
        img {
            display: block;
            max-width: 100%;
            height: auto;
            object-fit: cover;
            margin-bottom: 2.5rem;
        }
        a {
            color: $blue;
            &:hover {
                text-decoration: underline;
            }
        }
    }
    &__footer {
        display: flex;
        justify-content: space-between;
        align-items: center;
        
        @include breakpoint($mobile-bp) {
            flex-direction: column;
            align-items: flex-start;
        }
    }
    &__data {
        display: flex;

        color: #828282;
        line-height: 1.2;
        a:hover {
            text-decoration: underline;
        }
        &-item {
            & + &::before {
                content: "";
                margin: 0 1.5rem;
                position: relative;
                top: -1px;
                display: inline-block;
                width: 6px;
                height: 6px;
                border-radius: 50%;
                background-color: #c4c4c4;
            }
        }

        @include breakpoint($mobile-bp) {
            margin-bottom: 1rem;
            font-size: 1.2rem;
        }
    }
    &__read {
        font-size: 1.4rem;
        color: #107eff;
        &:hover {
            text-decoration: underline;
        }
    }
    &__actions {
        margin-bottom: 1.5rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 1.2rem;
        &-link {
            opacity: 0.6;
            transition: 0.2s linear;
        }
        &-link + &-link {
            padding-right: 2.8rem;
            position: relative;
            &::after {
                content: "";
                position: absolute;
                top: 0;
                right: 0;
                width: 20px;
                height: 20px;
                background: url(../../img/share.svg) center no-repeat;
            }
        }
        &-link:hover {
            text-decoration: underline;
            opacity: 1;
        }
        &-icon {
            margin-left: 0.5rem;
        }
    }
}
