.form {
    &--auth {
        max-width: 28rem;
        margin: 0 auto;
    }
    &__group {
        margin-bottom: 1.5rem;
        position: relative;
        &--md {
            margin-bottom: 2.5rem;
        }
    }
    &__control {
        display: block;
        padding: .5rem 0 .75rem;
        width: 100%;
        background: none;
        outline: none;
        border: none;
        border-radius: 0;
        border-bottom: 1px solid #3F3F3F;
        box-shadow: none;
        font-family: inherit;
        font-size: 1.4rem;
        color: $white;
        &:focus + .form__line {
            width: 100%;
        }
        &::placeholder {
            color: rgba($white, .2);
        }
        &--textarea {
            resize: none;
            overflow: hidden;
            height: 30px;
        }
    }
    &__line {
        transition: .2s linear;
        width: 0%;
        height: 1px;
        background-color: $blue;
        position: absolute;
        bottom: 0;
        left: 0;
    }
    &__footer {
        &--center {
            text-align: center;
        }
        &-list {
            display: flex;
            justify-content: center;
        }
        li {
            margin: 0 .5rem;
        }
    }
}
