.header {
    display: flex;

    position: fixed;
    top: 0;
    right: 0;
    z-index: $z-index-header;
    width: calc(100% - #{$sidebar-width});
    background-color: #0D0D0D;
    box-shadow: 0 5px 22px rgba($black, .25);

    @include breakpoint($tablet-bp) {
        width: 100%;
    }
    &__left {
        width: 60%;
        @include breakpoint($tablet-bp) {
            width: 40%;
        }
    }
    &__right {
        width: 40%;
        display: flex;
        justify-content: flex-end;
        @include breakpoint($tablet-bp) {
            width: 60%;
        }
    }
}

