.add-post {
    padding: 1.5rem 2rem;
    margin-bottom: 3rem;
    background-color: $secondary;
    box-shadow: 0 15px 25px rgba($black, .45);
    border-radius: 5px;

    @include breakpoint($mobile-bp) {
        padding: 1.5rem;  
    }
		&__form {
            display: flex;
            align-items: flex-end;
		}
        &__textarea {
            font-family: inherit;
            flex-grow: 1;
            overflow: hidden;
            width: 75%;
            height: 34px;
            border: none;
            background-color: transparent;
            outline: none;
            resize: none;
            font-size: 1.5rem;
            color: $white;
            &::placeholder {
                color: #d2d2d2;
            }
        }
        &__form-actions {
            display: flex;
            justify-content: flex-end;
            width: 12rem;
            flex-shrink: 0;
            @include breakpoint($mobile-bp) {
                width: 9rem;  
            }
        }
        &__file,
        &__send {
            display: block;
            width: 4.4rem;
            height: 4.4rem;
            cursor: pointer;
            border-radius: 50%;
            border: none;
            transition: .2s linear;

            @include breakpoint($mobile-bp) {
                width: 3.4rem;
                height: 3.4rem;  
            }
        }
        &__file {
            background: #EBEBEB
            url(/img/add-photo.svg) center no-repeat;
            input {
                display: none;
            }
            @include breakpoint($mobile-bp) {
                background-size: 2rem;
            }
            &:hover {
                background-color: darken(#EBEBEB, 5%);
            }
        }
        &__send {
            background: $blue url(../../img/send.svg) center no-repeat;
            margin-left: 1.5rem;
            font-size: 0;
            color: transparent;
            @include breakpoint($mobile-bp) {
                background-size: 1.5rem;
            }
            &:hover {
                background-color: darken($blue, 5%);
            }
        }
}
