.embed,
.fr-video {
    position: relative;
    overflow: hidden;
    display: block;
    &::before {
        content: '';
        display: block;
        padding-top: 56.25%;
    }

    iframe,
    video,
    object,
    embed {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 1;
        width: 100%;
        height: 100%;
    }
}