.pagination {
    display: flex;
    justify-content: center;
    &__item {
        margin: 0 .5rem;
    }
    &__link {
        display: block;
        box-shadow: 0 0 10px rgba($black, .15);
        width: 40px;
        height: 40px;
        background-color: #0D0D0D;
        border-radius: 3px;
        text-align: center;
        font-size: 1.4rem;
        transition: .2s linear;
        line-height: 4rem;
        &:hover {
            background-color: lighten(#0D0D0D, 3%);
        }
        &.active {
            background-color: $blue;
            box-shadow: 0 0 10px rgba($blue, .15);
        }
    }
}