.burger {
    display: none;
    width: 3rem;
    height: 2rem;
    position: absolute;
    top: 50%;
    left: 1.5rem;
    transform: translateY(-50%);
    background: none;
    border: none;
    padding: 0;
    border-radius: 0;
    font-size: 0;
    color: transparent;

    span,
    &::before,
    &::after {
        display: block;
        width: 100%;
        height: 2px;
        background-color: $white;

        position: absolute;
    }
    &::after {
        content: '';
        transition: .2s linear;
        bottom: 0;
    }
    &::before {
        content: '';
        transition: .2s linear;
        top: 0;
    }
    span {
        top: 50%;
        transform: translateY(-50%);
        transition: 0.2s linear;
    }
    .show-sidebar & {
        span {
            opacity: 0;
        }

        &::before,
        &::after {
            top: 50%;
        }

        &::before {
            transform: rotate(45deg);
        }
        &::after {
            transform: rotate(-45deg);
        }
    }
    @include breakpoint($tablet-bp) {
        display: block;
    }
}
