.work {
    margin-bottom: 3rem;
    background-color: $primary;
    box-shadow: 0 15px 25px rgba($black, 0.35);
    border-radius: 5px;
    overflow: hidden;
    display: flex;
    @include breakpoint($mobile-bp) {
        flex-direction: column;
        align-items: center;
    }
    &__preview {
        max-width: 31rem;
        flex-grow: 1;
        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        @include breakpoint($mobile-bp) {
            width: 100%;
            height: 15rem;
        }
    }
    &__content {
        max-width: 29rem;
        padding: 2.5rem;
        display: flex;
        flex-direction: column;
        @include breakpoint($mobile-bp) {
            max-width: none;
            padding: 1.5rem;
        }
    }
    &__title {
        font-size: 2.4rem;
        color: $white;
        font-weight: 500;
        margin-bottom: 3rem;
        @include breakpoint($mobile-bp) {
            margin-bottom: 1.5rem;
        }
    }
    &__description {
        color: #d2d2d2;
        font-size: 1.6rem;
        line-height: 1.7;
        font-weight: 300;
        margin-bottom: 3rem;
        @include breakpoint($mobile-bp) {
            margin-bottom: 1.5rem;
            font-size: 1.4rem;
        }
    }
    &__footer {
        margin-top: auto;
        text-align: right;
        @include breakpoint($mobile-bp) {
            text-align: left;
        }
    }
}