.modal {
    display: none;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba($black, .8);
    z-index: $z-index-modal;
    padding: 1rem;
    &.show {
        display: flex;
    }
    &__content {
        width: 100%;
        max-width: 50rem;
        padding: 8rem;
        background-color: $primary;
        border-radius: 5px;
        position: relative;
        opacity: 0;

        transform: translateY(-50px);
        transition: .2s linear;

        @include breakpoint($tablet-bp) {
            padding: 5rem 3rem 3rem;
        }
        &--contact {
            padding-bottom: 2.5rem;
        }
        &--story {
            padding: 0;
            background: none;
        }
    }
    &__close {
        position: absolute;
        top: 2rem;
        right: 2rem;
        padding: 0;
        background: transparent;
        border: none;
        cursor: pointer;
        transition: .2s linear;
        opacity: 0.5;
        &:hover {
            transform: rotate(90deg);
            opacity: 1;
        }
    }
    &__footer {
        display: flex;
        justify-content: center;
        margin-top: 4rem;
        @include breakpoint($sm-mobile-bp) {
            flex-direction: column;
            align-items: center;
        }
    }
    &__footer-item {
        margin: 0 .5rem;
        font-size: 1.2rem;
        color: #B2B2B2;
        a {
            text-decoration: underline;
            &:hover {
                text-decoration: none;
            }
        }
        @include breakpoint($sm-mobile-bp) {
            margin-top: .5rem;
            margin-bottom: .5rem;
        }
    }
    &__video {
        margin: 0 auto;
        display: block;
    }
}