.nav {
    @include breakpoint($tablet-bp) {
        display: none;
    }
    &--mobile {
        display: none;
        margin: 3rem 0;
        @include breakpoint($tablet-bp) {
            display: block;
        }
    }
    &__list {
        position: relative;
        z-index: 1000;
        display: flex;
        @include breakpoint($tablet-bp) {
            flex-direction: column;
        }
    }
    &__item {
        position: relative;
        z-index: 1000;
        transition: .2s linear;
        
        &:hover {
            background-color: $blue;
        }

        &:hover {
            > .subnav {
                display: block;
            }
        }

        @include breakpoint($tablet-bp) {
            &:hover,
            .nav__link:hover {
                background: none;
            }
        }
    }
    &__link {
        display: block;
        font-size: 1.1rem;
        text-transform: uppercase;
        color: $white;
        padding: 1.3rem;
        line-height: 1.2;
        transition: 0.2s linear;
        position: relative;
        z-index: 1000;
        &.has-subnav {
            padding-right: 3.5rem;
            position: relative;
            &::after {
                content: "";
                position: absolute;
                top: 50%;
                right: 1.35rem;
                margin-top: 2px;
                display: block;
                border-style: solid;
                border-width: 6px 5px 0 5px;
                border-color: $white transparent transparent transparent;
                transform: translateY(-50%);
            }
        }
        @include breakpoint($tablet-bp) {
            padding-left: 0 !important;
            padding-right: 0 !important;
    
            font-size: 1.5rem;
        }
    }
}
