.stories {
    display: grid;
    grid-template: 195px / repeat(4, 1fr);
    grid-column-gap: 13px;
    margin-bottom: 3rem;

    @include breakpoint($mobile-bp) {
        grid-template-columns: repeat(4, 14rem);
        overflow: auto;
        &::-webkit-scrollbar {
            display: none;
        }
    }

    &__item {
        border-radius: 7px;
        overflow: hidden;
        box-shadow: 0 5px 15px rgba($black, 0.2);
        position: relative;
        cursor: pointer;

        &::after {
            content: "";
            position: absolute;
            display: block;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            z-index: 2;
            background: linear-gradient(to bottom, rgba($black, 0.8), rgba($black, 0) 50%, rgba($black, 0.8));
        }
        &:hover {
            .stories__preview {
                transform: scale(1.3);
                transition: 2s;
            }
        }
    }
    &__preview {
        transition: .5s linear;
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 1;
        object-fit: cover;
    }
    &__title,
    &__date {
        font-size: 1.2rem;
        padding: 1.4rem;
        color: $white;
        position: absolute;
        left: 0;
        line-height: 1.3;
        width: 100%;
        z-index: 3;
    }
    &__title {
        top: 0;
    }

    &__date {
        bottom: 0;
        text-align: right;
    }
}
