.related {
    padding-bottom: .5rem;
    &__list {
        display: flex;
        flex-wrap: wrap;
        @include breakpoint($mobile-bp) {
            flex-direction: column;
        }
    }
    &__list-item {
        margin-bottom: 2.5rem;
        width: 50%;
        @include breakpoint($mobile-bp) {
            width: 100%;
        }
    }
    &__list-title {
        font-size: 1.4rem;
        color: #F3EBA2;
        font-weight: 500;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        a:hover {
            text-decoration: underline;
        }
    }
    &__list-date {
        font-size: 1.2rem;
        color: #828282;
    }
}